.main-header {
    height: 100px;
    position: sticky;
    top: 0;
    transition: 0.4s;
    z-index: 4;
    background-color: rgb(255, 255, 255);
    border-bottom: 2px solid #241f1c;
    font-size: 1.3rem;

    img {
        width: 11rem;
        @include for-mobile-layout {
            width: 8rem;
        }
    }

    .ffu-logo {
        margin: 0;
    }

    .categories {
        display: none;
    }

    &.smaller {
        a {
            height: 100%;
        }

        height: 5rem;
        z-index: 2;

        .categories {
            display: block;
        }
    }

    &.transparent {
        border-bottom: none;
        position: fixed;
        width: 100%;
        background-color: rgba(255, 255, 255, 0);

        input,
        .search-btn {
            display: none;
        }

        &.white {
            background-color: rgb(255, 255, 255);

            input,
            .search-btn {
                display: block;
            }

            border-bottom: 2px solid #241f1c;
        }
    }

    @include for-mobile-layout {
        .search {
            font-size: 0.7rem;
            width: 185px;
        }
    }
}

.main-nav {
    margin: 0;
    padding: 0;
    margin-right: 1rem;

    li:not(:last-child) {
        margin-inline-end: 40px;
    }

    li {
        display: flex;

        a {
            color: #241f1c;
            font-weight: bold;
            padding: 2.5rem 0;
            align-self: center;
            position: relative;
        span {
            width: 17px;
            font-family: inherit;
            right: -13px;
            top: 30px;
            text-align: center;
            position: absolute;
            border-radius: 50%;
            height: 17px;
            font-size: 0.8rem;
            font-size: 1rem;
            font-weight: bold;
            padding: 0;
            margin: 0;
            color: white;
            background: #bd1604;
        }
        }
    }
}

.container {
    margin: 0 auto;
    padding-left: 0;
    padding-right: 0.6rem;
}

.btn-menu {
    display: none;
    background-color: rgba(255, 255, 255, 0);
    border: none;
    font-size: 1.5rem;
    color: #241f1c;
}

@media (max-width: 840px) {
    .btn-menu {
        display: block;
    }

    .main-nav {
        position: fixed;
        top: 0;
        right: 0;
        flex-direction: column;
        height: 100vh;
        background-color: #fff;
        border-left: 1px solid #e8e8e8;
        min-width: 200px;
        transform: translateX(100%);
        transition: transform 0.6s;
        margin-right: 0;
    }

    .menu-open {
        transform: translateX(0);
    }

    .main-nav li {
        margin: 0 !important;
        border-bottom: 1px solid #e8e8e8;
        text-align: center;
    }

    .main-nav li a span {
        right: 75px;
    }
    .main-nav a {
        transition: background-color 0.3s;
        width: 100%;
        
    }

    .main-nav a:hover {
        background-color: #e8e8e8;
    }
}

.screen {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.6);
    cursor: pointer;
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.6s;

    &.menu-open {
        visibility: visible;
        opacity: 1;
    }
}
.reaction{
    background-color: #fff;
    position: fixed;
    width: 100%;
    text-align: center;
    z-index: 4;
    padding: 0 2rem;
    top: 0.2rem;
    border: 4px solid rgb(86, 192, 36);
    border-radius: 2rem;
    transition: 2.4s;
    -webkit-animation-name: animatetop;
    -webkit-animation-duration: 0.4s;
    animation-name: animatetop;
    animation-duration: 0.4s;

}
@-webkit-keyframes animatetop {
    from {
        top: -300px;
        opacity: 0;
    }

    to {
        top: 0;
        opacity: 1;
    }
}

@keyframes animatetop {
    from {
        top: -300px;
        opacity: 0;
    }

    to {
        top: 0;
        opacity: 1;
    }
}
