.login-container {
    max-width: 450px;
    min-width: 300px;
    margin: 30px auto;
    padding: 0 15px;
    .signup,
    .login {
        width: 50%;
        background: #fff;
        float: left;
        height: 60px;
        line-height: 60px;
        text-align: center;
        cursor: pointer;
        text-transform: uppercase;
        border-bottom: 1px solid #6c6c6e;
    }

    .login {
        border-right: 1px solid #6c6c6e;
    }

    .shown {
        border-top: 1px solid #6c6c6e;
        border-right: 1px solid #6c6c6e;
        border-left: 1px solid #6c6c6e;
        border-bottom: none;
    }

    form {
        padding: 40px;
        clear: both;
        width: 100%;
        box-sizing: border-box;
        height: 400px;

        input {
            width: 100%;
            padding: 20px;
            box-sizing: border-box;
            margin-bottom: 25px;
            border: 2px solid #e9eaea;
            color: #3e3e40;
            font-size: 14px;
            outline: none;
            transform: all 0.5s ease;
        }

        input:focus {
            border: 2px solid olive;
        }

        button {
            border: 2px solid black;
            width: 100%;
            background: #3e3e40;
            height: 50px;
            text-align: center;
            line-height: 50px;
            text-transform: uppercase;
            color: #fff;
            font-weight: bold;
            letter-spacing: 1px;
            cursor: pointer;
            margin-bottom: 30px;
        }
    }
}

.logout{
    padding-right: 5px;
    display: flex;
    width: 100%;
    justify-content: space-between;
}

.logout-btn {
    border: 2px solid black;
    width: 100px;
    background: #3e3e40;
    height: 30px;
    text-align: center;
    line-height: 30px;
    text-transform: uppercase;
    color: #fff;
    font-weight: bold;
    letter-spacing: 1px;
    cursor: pointer;
    margin-top: 20px;
}
